<template>
    <div class="Blogs">
        <p class="blog-title">{{blog.title}}</p>
        <div class="blog-body">
            <div class="paragraph" v-html="blog.body">
            </div>
        </div>
    </div>
</template>
<style lang="scss">
    .Blogs {
        .blog-title {
          font-size: 30px;
          font-weight: bold;
        }
        .blog-body {
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            padding: 10px;
            text-align: justify;
        }
        .update, .delete {
            font-size: 18px;
            font-weight: 600;
        }
        .update:hover, .delete:hover {
            color: red;
        }
    }
</style>

<script>
    export default{
        data(){
            return{
                blog:''
            }
        },
        mounted:function(){
            var self = this;
            var queryData = this.$route.query;
            this.$http.get("static/file/blogs/"+queryData["_id"]+".json").then(function(res){
                self.blog=res.body;
            })
        },
        methods : {
        },
        components:{
        }
    }
</script>



