<template>
  <div class="Welcome">
    <div class="Welcome-content">
      <div class="recentActive">
        <ul>
          <li v-for="item in msg">
            <h2>
              <router-link :title="item.title" class="Welcome-title"
                           :to="{ path: '/Blogs',query: { _id: item._id }}">
                【{{item.type}}】{{item.title}}
              </router-link>
            </h2>
            <div class="roughContent">
              {{item.intro||""}}
            </div>
            <div class="feed-meta">
              <data>{{item.time}}</data>
              <router-link :title="item.title" class="readAll"
                           :to="{ path: '/Blogs',query: { _id: item._id }}">
                [阅读全文]
              </router-link>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
  .Welcome {
    overflow: hidden;
    .Welcome-sort, .Welcome-title, .readAll {
      color: #222;
      &:hover {
        color: #000;
        text-decoration: underline;
      }
    }
  }

  .Welcome-content {
    margin: 0 auto;
    width: 950px;
    background-color: #fff;
  }

  .roughContent {
    font-size: 14px;
    margin: 10px 0;
    line-height: 20px;
    text-indent: 1em;
  }

  .recentActive {
    h1 {
      font-size: 24px;
      color: #999;
    }
    h2 {
      font-size: 25px;
      font-weight: 700;

    }
    data {
      height: 30px;
      line-height: 30px;
      font-size: 14px;
      color: #828282;
      font-weight: 600;
    }
    .readAll {
      float: right;
      height: 30px;
      font-weight: 700;
      font-size: 14px;
      line-height: 30px;
    }
  }

  .recentActive ul {
    box-sizing: border-box;
    margin-top: 20px;
  }

  .recentActive li {
    word-break: break-all;
    margin-top: 10px;
  }

  @media screen and (max-width: 786px) {
    .wrap {
      width: 90%;
      box-sizing: border-box;
      padding-top: 40px;
      margin: 0 auto;
    }
    .Welcome-content {
      width: 100%;
    }
    .recentActive h2 {
      font-size: 18px;
      font-weight: 600;
    }
  }
</style>
<script>
    export default{
        data(){
            return {
                msg: ''
            }
        },
        mounted: function () {
            var self = this
            this.$http.get("static/file/blog.json").then(function (res) {
                self.msg = res.body;
            })
        },
        methods: {
        }
    }

</script>



