<template>
  <div class="header">
    <nav class="nav">
      <ul class="navbar">
        <li v-bind:class="{active:clickIndex==1}" @click="clickNav(1)">
          <router-link to="/" >首页</router-link>
        </li>
        <li v-bind:class="{active:clickIndex==2}" @click="clickNav(2)">
          <router-link :to="{path:'/TimeLine'}" >时·归档</router-link>
        </li>
        <li>
          <a href="https://github.com/rrrrui" target="_blank">GitHub</a>
        </li>
      </ul>
    </nav>
  </div>
</template>
<style lang="scss">
  .nav {
    width: 1060px;
    height: 50px;
    line-height: 50px;
    margin: 0 auto;
  }

  .myPic {
    width: 40px;
    height: 40px;
    line-height: 39px;
    margin-top: 4px;
    overflow: hidden;
    border-radius: 20px;
    float: left;
  }

  .navbar li a {
    display: inline-block;
    width: 100%;
    height: 50px;
    font-weight: 700;
  }

  .navbar li a:hover {
    font-weight: 700;
    transition: all 300ms ease-out;
  }

  .active {
    background-color: #e3e3e3;
  }

  .nav ul li {
    display: inline-block;
    font-size: 18px;
    text-align: center;
    width: 100px;
    margin-left: 50px;
  }

  @media screen and (max-width: 786px) {
    .nav {
      width: 100%;
      height: 50px;
      line-height: 50px;
      margin: 0 auto;
      ul {
        li {
          width: 32%;
          margin: 0;
        }
      }
    }
  }
</style>
<script>
  export default{
    data(){
      return {
          clickIndex: 1
      }
    },
    mounted: function () {
    },
    methods: {
      "clickNav" : function(num){
        this.clickIndex = num;
      }
    }
  }

</script>



