<template>
  <div class="time-content webAndStudy">
    <div class="forDate" v-for="i in types">
      <span class="circle"></span>
      <h1 class="modle-title">{{i[0].type}}</h1>
      <ul>
        <li v-for="item in i">
          <h2>
            <router-link :title="item.title" class="title" :to="{ path: '/Blogs',query:{_id:item._id}}">
              {{item.title}}
            </router-link>
          </h2>
          <div class="roughContent">
            {{item.intro}}
          </div>
          <div class="feed-meta">
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<style lang="scss">
  .time-content {
    .forDate {
      border-left: 10px solid #5adc94;
      padding: 30px 10px;
    }
    .circle {
      display: block;
      width: 20px;
      height: 20px;
      background: #5adc94;
      border-radius: 10px;
      position: relative;
      left: -25px;
      top: 27px;
    }
  }
</style>
<script>
    export default{
        data(){
            return{
                msg:"",
                types:""
            }
        },
        mounted:function(){
            var self = this
            this.getData();
        },
        methods : {
            "getData" : function(){
                this.$http.get("static/file/blog.json").then(function(res){
                   this.msg = res.body;
                   var types = {}
                   for(var i in this.msg){
                        let obj = this.msg[i];
                        if(!types[obj.type]){
                            types[obj.type] = [obj];
                        }else{
                            types[obj.type].push(obj)
                        }
                   }
                   this.types = types;
                })
            }
        },
        components:{
        }
    }

</script>



